<template>
  <div class="warp">
    <div style="margin-bottom: 20px">
      <el-dropdown>
        <el-button
          type="primary"
          style="background-color: #e9f3fb; color: #279dff"
        >
          角色状态<i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>黄金糕</el-dropdown-item>
          <el-dropdown-item>狮子头</el-dropdown-item>
          <el-dropdown-item>螺蛳粉</el-dropdown-item>
          <el-dropdown-item>双皮奶</el-dropdown-item>
          <el-dropdown-item>蚵仔煎</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <div style="width: 312px; display: inline-block; margin-left: 20px">
        <el-input
          v-model="input"
          placeholder="请输入角色名称进行模糊搜索"
          style="
            width: 86%;
            margin-top: -6px;
            border-top-right-radius: 0px;
            border-bottom-right-radius: 0px;
          "
        >
        </el-input>
        <div class="wrap_conter" @click="search()">
          <i class="el-icon-search"></i>
        </div>
      </div>
      <div style="float: right" @click="add()">
        <el-button type="primary">+&nbsp;添加系统角色</el-button>
      </div>
    </div>

    <el-table
      :data="tableData"
      style="width: 100%"
      ref="multipleTable"
      @select="ppp"
      @select-all="qx"
    >
      <el-table-column type="selection" width="55" :style="a">
      </el-table-column>

      <el-table-column label="角色名称" width="180">
        <template slot-scope="scope">
          <span>{{ scope.row.rolename }}</span>
        </template>
      </el-table-column>
      <el-table-column label="角色描述" width="180">
        <template slot-scope="scope">
          <span>{{ scope.row.describe }}</span>
        </template>
      </el-table-column>
      <el-table-column label="角色权限配置" width="180">
        <template slot-scope="scope">
          <span>{{
            scope.row.powers
              .replace("0", "开放所有权限")
              .replace("1", "首页")
              .replace("2", "企业管理")
              .replace("3", "通话统计")
              .replace("4", "并发统计")
              .replace("5", "权限管理")
              .replace("6", "系统校色管理")
              .replace("7", "系统用户管理")
              .replace("8", "系统配置")
              .replace("9", "呼转计费")
              .replace("10", "客户端版本维护")
          }}</span>
        </template>
      </el-table-column>
      <el-table-column label="角色启用状态" width="180">
        <template slot-scope="scope">
          <div
            :class="scope.row.status ? 'green' : 'red'"
            style="display: inline-block"
          ></div>
          <span>{{ scope.row.status ? "启用中" : "已停用" }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="date" label="最后编辑时间" sortable width="180">
        <template slot-scope="scope">
          <span>{{ formattedDate(scope.row.lasttime) }}</span>
        </template>
      </el-table-column>

      <el-table-column label="操作">
        <template slot-scope="scope">
          <i
            class="el-icon-edit icon"
            @click="handleEdit(scope.$index, scope.row)"
          ></i>
          <i
            class="el-icon-delete icon"
            @click="handleDelete(scope.$index, scope.row)"
          ></i>
          <i
            :class="
              scope.row.status
                ? 'el-icon-switch-button icon'
                : 'el-icon-video-play icon'
            "
            @click="stop(scope.$index, scope.row)"
          ></i>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="收货地址" :visible.sync="dialogFormVisible">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="活动名称" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="describe">
          <el-input v-model="ruleForm.describe"></el-input>
        </el-form-item>
        <el-form-item label="是否启用" prop="resource">
          <el-radio-group v-model="ruleForm.resource">
            <el-radio label="启用"></el-radio>
            <el-radio label="停用"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="权限配置" prop="authority">
          <div>
            <el-tree
              :data="arr"
              show-checkbox
              default-expand-all
              node-key="id"
              ref="tree"
              highlight-current
              :props="defaultProps"
              :default-checked-keys="abcd"
            >
            </el-tree>
            <div class="buttons">
              <!-- <el-button @click="getCheckedNodes">通过 node 获取</el-button> -->
              <!-- <el-button @click="getCheckedKeys">通过 key 获取</el-button> -->
              <el-button @click="resetChecked">清空权限配置</el-button>
            </div>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"
            >添加</el-button
          >
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <div class="buttom">
      <div style="float: left">
        <p @click="toggleSelection(tableData)">
          已选 <span>{{ selectedRowCount }}</span> 条
        </p>
        <p @click="BatchDel()">批量删除</p>
        <p @click="BatchStop()">批量启用/停用角色</p>
      </div>
      <div style="float: right">
        <div class="block">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage4"
            :page-size="suum"
            layout="total, sizes, prev, pager, next, jumper"
            :total="sum"
            :page-count="10"
          >
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getsystem,
  getsystemdel,
  getsystemss,
  getsystemadd,
  getsystemstop,
} from "../../api/https";
export default {
  data() {
    return {
      abcd:[],
      suum: 10,
      sum: 1,
      currentPage4: 1,
      selectedRowCount: 0,
      selectedRows: [],
      a: { checked: true },
      props: {
        label: "name",
        children: "zones",
      },
      power: "",
      count: 1,
      tableData: [],
      input: "",
      name: "",
      dialogFormVisible: false,
      status: "",
      ruleForm: {
        name: "",
        describe: "",
        authority: "",
        resource: "",
      },
      ary: [],
      rules: {
        name: [
          { required: true, message: "请输入角色名称", trigger: "blur" },
          { max: 8, message: "不能大于8个字符", trigger: "blur" },
        ],
        resource: [{ required: true, message: "是否启用", trigger: "change" }],
        authority: [{ required: true, message: "请选择", trigger: "blur" }],
      },
      arr: [
        {
          id: 0,
          label: "开放所有权限",
          children: [
            { id: 1, label: "首页" },
            { id: 2, label: "企业管理" },
            {
              id: 3,
              label: "通话统计",
              children: [{ id: 4, label: "并发统计" }],
            },
            {
              id: 5,
              label: "权限管理",
              children: [
                { id: 6, label: "系统角色管理" },
                { id: 7, label: "系统用户管理" },
              ],
            },
            {
              id: 8,
              label: "系统配置",
              children: [
                { id: 9, label: "呼转计费" },
                { id: 10, label: "客户端版本维护" },
              ],
            },
          ],
        },
      ],
      defaultProps: {
        children: "children",
        label: "label",
      },
    };
  },
  methods: {
    // 编辑
    handleEdit(index, row) {
      this.dialogFormVisible = true;
      let obj = {
        id: row.id,
        rolename: row.rolename,
        describe: row.describe,
        status: row.resource == "启用" ? 1 : 0,
        powers: row.powers,
      };
      this.ruleForm.name = row.rolename;
      this.ruleForm.describe = row.describe;
      this.ruleForm.resource = row.status ? "启用" : "停用";
      this.abcd = row.powers.split(",");

      // getsystemadd(obj).then((res) => {
      //   console.log(res);
      //   this.fun();
      //   this.resetChecked()
      //   this.dialogFormVisible = false;
      //   this.ruleForm.name = "";
      //   this.ruleForm.describe = "";
      // });
      console.log(index, row, obj);
    },
    // 每页显示几条
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.fun({ pagelimit: val });
    },
    // 去第几页
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.fun({ page: val });
    },
    // 批量启用
    BatchStop() {
      this.ary.forEach((item) => {
        item.id;
      });
    },
    // 批量删除
    BatchDel() {
      this.$confirm(`确定要删除吗`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.ary.forEach((item) => {
            console.log(item.id);
            getsystemdel(item.id).then((res) => {
              console.log(res);
              this.fun();
            });
            this.$message({
              type: "success",
              message: "删除成功!",
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 点击多选框的全选
    qx(item) {
      this.selectedRowCount = item.length;
      console.log(item);
    },
    // 点击多选
    ppp(item) {
      this.selectedRowCount = item.length;
      this.ary = item;
    },
    // 停用
    stop(index, row) {
      if (row.status == 1) {
        this.status = "停用";
      } else {
        this.status = "启用";
      }
      this.$confirm(`确认${this.status}${row.rolename}角色？`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          let obj = { id: row.id, status: !row.status };
          getsystemstop(obj).then((res) => {
            console.log(res, row.status);
            this.fun();
          });
          this.$message({
            type: "success",
            message: "停用成功!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消停用",
          });
        });
    },

    // 转化日期格式
    formattedDate(num) {
      const date = new Date(num * 1000);
      return `${date.getFullYear()}-${pad(date.getMonth() + 1)}-${pad(
        date.getDate()
      )} ${pad(date.getHours())}:${pad(date.getMinutes())}:${pad(
        date.getSeconds()
      )}`;

      function pad(n) {
        return n < 10 ? "0" + n : n;
      }
    },
    // // getCheckedNodes() {
    // //   console.log(this.$refs.tree.getCheckedNodes());
    // // },
    // getCheckedKeys() {
    //   console.log((this.$refs.tree.getCheckedKeys()).join(","));
    // },
    // 清空选择的权限
    resetChecked() {
      this.$refs.tree.setCheckedKeys([]);
    },

    // 重置
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 点击添加
    submitForm() {
      let obj = {
        rolename: this.ruleForm.name,
        describe: this.ruleForm.describe,
        status: this.ruleForm.resource == "启用" ? 1 : 0,
        powers: this.$refs.tree.getCheckedKeys().join(","),
      };
      getsystemadd(obj).then((res) => {
        console.log(res);
        this.fun();
        this.resetChecked();
        this.dialogFormVisible = false;
        this.ruleForm.name = "";
        this.ruleForm.describe = "";
      });
    },
    // 弹出添加的弹框
    add() {
      this.dialogFormVisible = true;
    },
    // 搜索
    search() {
      getsystemss({ rolename: this.input }).then((res) => {
        this.tableData = res.data.data;
      });
    },
    // 获取
    fun(obj) {
      getsystem(obj).then((res) => {
        this.tableData = res.data.data;
        this.sum = res.data.pagecount;
      });
    },
    // 删除
    handleDelete(row) {
      this.$confirm(`删除后，${row.rolename}将不再具备角色响应的权限`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          getsystemdel(row.id).then((res) => {
            console.log(res);
            this.fun();
          });
          this.$message({
            type: "success",
            message: "删除成功!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
  },
  created() {
    this.fun();
  },
};
</script>

<style scoped>
.buttom p span {
  color: orange;
}
.buttom p {
  float: left;
  margin-left: 24px;
  font-size: 14px;
  color: #ccd3e0;
  line-height: 40px;
}
.buttom p:nth-child(1) {
  color: black;
}
.buttom {
  margin-top: 20px;
  background-color: #ffffff;
  width: 100%;
  height: 42px;
}
.green {
  display: inline-block;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  border: none;
  background-color: #38d88f;
  margin-right: 5px;
  margin-bottom: 3px;
}
.red {
  display: inline-block;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  border: none;
  background-color: #ff000d;
  margin-right: 5px;
  margin-bottom: 3px;
}
.icon:hover {
  color: #409eff;
}
.icon {
  margin-left: 10px;
}
.icon:nth-child(1) {
  margin-left: 0px;
}
.wrap_conter {
  width: 13%;
  display: inline-block;
  text-align: center;
  background-color: #f5f7fa;
  height: 100%;
  line-height: 39px;
  border-radius: 5px;
  border: solid 1px #dcdfe6;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.el-dropdown {
  vertical-align: top;
}
.el-dropdown + .el-dropdown {
  margin-left: 15px;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.warp {
  width: 94%;
  background-color: white;
  margin: auto;
  margin-top: 20px;
  padding: 20px;
}
</style>
